.container{padding:50px 0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.card:not(:last-of-type){margin-bottom:20px}.card .message{display:flex}.card .message img{width:50px !important;height:50px !important;margin-right:15px}.card .message .username{display:inline-block;font-weight:bold}.card .message-body{display:block}

